.header-m .nav-menu {
	position: relative;
	z-index: 200;
	height: 42px;
	color: #222
}
.header-m .nav-menu .nav-mask {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: hsla(0,0%,100%,.4);
	-webkit-box-shadow: rgba(0,0,0,.1) 0 1px 2px;
	box-shadow: 0 1px 2px rgba(0,0,0,.1)
}
.header-m .nav-menu .nav-con .nav-item {
	float: left;
	text-align: center;
	line-height: 42px;
	height: 42px;
	position: relative;
	background-color: hsla(0,0%,100%,0);
	-webkit-transition: all .3s;
	transition: all .3s
}
.header-m .nav-menu .nav-con .nav-item .t {
	color: #222;
	height: 100%;
	display: block;
	padding: 0 11px;
	cursor: pointer
}
.header-m .nav-menu .nav-con .nav-item:hover {
	background-color: hsla(0,0%,100%,.3)
}

